___CSS_LOADER_EXPORT___.push([module.id, ".balance-legend{color:#0d274b;font-size:40px}.active-tour,.balance-legend{font-family:Open Sans;font-weight:900}.active-tour{background:#f08b23;border-radius:20px;box-shadow:0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.12),0 1px 5px 0 rgba(0,0,0,.2);padding:3px 20px;text-align:center;text-transform:uppercase;width:206px}.white--text{caret-color:#fff!important;color:#fff!important}", "",{"version":3,"sources":["webpack://./resources/js/desktop/pages/Client/Balance.vue"],"names":[],"mappings":"AA8IA,gBAGA,aAAA,CACA,cACA,CACA,6BALA,qBAAA,CACA,eAeA,CAXA,aACA,kBAAA,CACA,kBAAA,CAGA,gGAAA,CAFA,gBAAA,CAOA,iBAAA,CAHA,wBAAA,CAEA,WAEA,CACA,aAEA,0BAAA,CADA,oBAEA","sourcesContent":["<template>\n    <div class=\"row m-auto align-items-center justify-content-center\" v-if=\"tour!=null\n\" style=\"width: 100%; height: 100%\">\n        <div class=\"col-md-4 col-sm-12\">\n           <div class=\"row align-items-center justify-content-center m-auto\" style=\"height: 100%; width: 100%\">\n               <vue-ellipse-progress :progress=\"progress\" :color=\"'#f08b23'\" :dot=\"{ size: '15px', backgroundColor: '#0d274b', }\" :legendClass=\"'balance-legend'\">\n                   <span slot=\"legend-value\">%</span>\n               </vue-ellipse-progress>\n           </div>\n        </div>\n        <div class=\"col-md-8 col-sm-12\">\n            <div class=\"row m-auto\" style=\"width: 100%; height: 100%\">\n                <div class=\"col-md-12\">\n                    <div class=\"row mx-auto mt-5 mb-5 justify-content-center align-items-center\" style=\"width: 100%;border-top: 4px solid #f08b23;\">\n                        <span class=\"white--text active-tour\" style=\"background:#062348; font-size: 20px;position: absolute\">Общая сумма</span>\n                    </div>\n                    <div class=\"row mx-auto mt-5 mb-5 justify-content-center align-items-center\" style=\"width: 100%;\">\n                        <span style=\"color: #0f213d; font-family: 'Open Sans'; font-weight: 900; font-size: 24px;\">{{tour.TourInfo.price}}</span>\n                    </div>\n                </div>\n                <div class=\"col-md-6\">\n                    <div class=\"row mx-auto mt-5 mb-5 justify-content-center align-items-center\" style=\"width: 100%;border-top: 4px solid #f08b23;\">\n                        <span class=\"white--text active-tour\" style=\"background:#062348; font-size: 20px;position: absolute\">Выплачено</span>\n                    </div>\n                    <div class=\"row mx-auto mt-5 mb-5 justify-content-center align-items-center\" style=\"width: 100%;\">\n                        <span style=\"color: #0f213d; font-family: 'Open Sans'; font-weight: 900; font-size: 24px;\">{{tour.TourInfo.paid}}</span>\n                    </div>\n\n                </div>\n                <div class=\"col-md-6\">\n                    <div class=\"row mx-auto mt-5 mb-5 justify-content-center align-items-center\" style=\"width: 100%;border-top: 4px solid #f08b23;\">\n                        <span class=\"white--text active-tour\" style=\"background:#062348; font-size: 20px;position: absolute\">Остаток</span>\n                    </div>\n                    <div class=\"row mx-auto mt-5 mb-5 justify-content-center align-items-center\" style=\"width: 100%;\">\n                        <span style=\"color: #0f213d; font-family: 'Open Sans'; font-weight: 900; font-size: 24px;\">{{tour.TourInfo.left}}</span>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\n    export default {\n        name: \"Balance\",\n        data: () => ({\n            progress: 0,\n            sum: 13000,\n            paid: 1000,\n            left: 0,\n            tour:null\n        }),\n        created() {\n            this.getUser()\n        },\n\n        mounted() {\n        },\n        computed:\n        {\n            menu() {\n                return this.$store.getters.menu\n            },\n            user() {\n                return this.$store.getters.user\n            },\n            preloader() {\n                return this.$store.getters.preloader\n            },\n        },\n        methods:\n        {\n            async getUser() {\n                if (!this.user) {\n                    await this.$store.dispatch('getUser').then( () => {\n                        this.tour = this.user.tours.find((tour) => tour.id == this.$route.params.id);\n                    })\n                }\n                else{\n                    this.tour = this.user.tours.find((tour) => tour.id == this.$route.params.id);\n                }\n                this.progress = (this.tour.TourInfo.paid/this.tour.TourInfo.price)*100;\n                this.progress = this.decimalAdjust('round', this.progress, -2);\n                this.left = this.tour.TourInfo.left;\n\n            },\n            decimalAdjust(type, value, exp) {\n                // Если степень не определена, либо равна нулю...\n                if (typeof exp === 'undefined' || +exp === 0) {\n                    return Math[type](value);\n                }\n                value = +value;\n                exp = +exp;\n                // Если значение не является числом, либо степень не является целым числом...\n                if (isNaN(value) || !(typeof exp === 'number' && exp % 1 === 0)) {\n                    return NaN;\n                }\n                // Сдвиг разрядов\n                value = value.toString().split('e');\n                value = Math[type](+(value[0] + 'e' + (value[1] ? (+value[1] - exp) : -exp)));\n                // Обратный сдвиг\n                value = value.toString().split('e');\n                return +(value[0] + 'e' + (value[1] ? (+value[1] + exp) : exp));\n            },\n            sendMessage(message) {\n                this.$notify({\n                    group: 'info',\n                    type: 'travel',\n                    title: 'Сообщение от TravelClub',\n                    text: message\n                });\n            },\n        }\n    }\n</script>\n\n<style>\n    .balance-legend{\n        font-family: \"Open Sans\";\n        font-weight: 900;\n        color:#0d274b;\n        font-size: 40px;\n    }\n    .active-tour {\n        background: #f08b23;\n        border-radius: 20px;\n        padding: 3px 20px;\n        -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);\n        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);\n        font-weight: 900;\n        text-transform: uppercase;\n        font-family: 'Open Sans';\n        width: 206px;\n        text-align: center;\n    }\n    .white--text {\n        color: #fff !important;\n        caret-color: #fff !important;\n    }\n\n</style>\n"],"sourceRoot":""}]);
